.contact-us-container{
    font-size: 10px;
    margin-bottom: 40px;
}
.contact-form .form-group{
    margin-bottom: 20px;
    text-align: left;
}
.contact-form label{
    display: block;
    font-size: 16px;
    margin-bottom: 5px;
}
.contact-form input,.contact-form textarea{
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
}
.submit-btn{
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4caf50;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}
.submit-btn:hover{
    background-color: #45a049;
}